<template lang="pug">
    b-card(@click.left="onClick")
        h3 {{info.title}}
        template(v-if="info.confObj")
            b-badge.m-1(v-for="i in info.confObj.tags"
                @click.left.stop="$emit('tagclick', i)"
                :variant="$parent.tags[i] ? 'info' : ''"
            ) {{i}}
</template>

<script>
    export default {
        name: "ArticleEntry",

        props: {
            info: Object,
        },

        methods: {
            onClick() {
                window.location = this.$router.resolve({name: 'blog/' + this.info.title}).href
            }
        }
    }
</script>

